$PrimaryColor: black;
$SecondaryColor: white;

$HighlightColor: red;

@mixin changeButtonColours ($p1, $p2) {
  background-color: $p1;
  color: $p2;
  // border-color: $p2;
}

.button1 {
  border: 1px solid $PrimaryColor;
  border-radius: 0 10px 0 10px;
  transition: all 0.3s ease-out;
  padding: 0.8rem;
  font-family: Sans-serif;
  font-size: 1rem;
  @include changeButtonColours($SecondaryColor, $PrimaryColor);
  // background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(9,9,121,0) 50%, rgba(0,0,0,1) 100%);
  // background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(8,8,109,0) 30%, rgba(9,9,121,0) 70%, rgba(0,0,0,1) 100%);
  // background: linear-gradient(0deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 50%, rgba(0,0,0,0) 100%);
  // color:white;
  // background: linear-gradient(90deg, rgb(104, 216, 210) 0%,rgba(255, 255, 255, 0.5) 100%);

  &:hover {
    @include changeButtonColours($PrimaryColor, $SecondaryColor);
  }
}