.sign-in {
  background-color: rgb(255, 255, 255);
  // margin: 20px;
  max-width: 50%;
  min-width: 200px;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  // justify-content: flex-end;
  // align-content: flex-end;

  padding: 20px;

  p{
    font-weight: 800;
  }

  input {
// text-decoration: none;

    display: block;
    width: 200px;
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;

    &::placeholder {
      color: rgb(177, 177, 177);
    }


    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
  }

  .submit-button{
    border: 1px solid black;

    &:hover{
      border-color: red;
      // background-color: red;

    }
  }
}